.App {
  font-family: Arial, sans-serif;
  text-align: center;
}

.navbar {
  background-color: #333;
  overflow: hidden;
}

.nav-links {
  display: flex;
  justify-content: center;
  list-style-type: none;
  margin: 0;
  padding: 0;
}

.nav-links a {
  color: #000; /* set default color */
  transition: color 0.2s ease-in-out; /* add transition effect */
}

.nav-links a:hover {
  color: #f00; /* change color on hover */
}


.nav-links li {
  margin: 10px;
}

.nav-links li a {
  color: #fff;
  text-decoration: none;
  font-size: 18px;
  font-weight: bold;
  padding: 10px 15px;
  transition: background-color 0.2s ease-in-out; /* add transition effect */
}

.nav-links li a:hover {
  background-color: #fff;
  color: #333;
  border-radius: 5px;
}

.hero-section {
  background-image: url("https://example.com/your-image.png");
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  height: 500px;
  display: flex;
  justify-content: center;
  align-items: center;
}

.hero-text {
  background-color: rgba(255, 255, 255, 0.8);
  border-radius: 5px;
  padding: 20px;
  max-width: 500px;
  text-align: center;
}

.hero-text h1 {
  font-size: 48px;
  margin-bottom: 20px;
}

.hero-text p {
  font-size: 24px;
  margin-bottom: 30px;
}

.hero-text button {
  background-color: #333;
  color: #fff;
  border: none;
  border-radius: 5px;
  font-size: 24px;
  padding: 10px 20px;
  cursor: pointer;
  transition: background-color 0.2s ease;
}

.hero-text button:hover {
  background-color: #fff;
  color: #333;
}
